html {
  background: rgb(241, 241, 137);
}

img {
  border-radius: 10px;
  box-shadow: 3px 2px 3px 0.6px rgb(75, 67, 67);
  width: 35vw;
}

p {
  background: rgb(242, 204, 210);
  height: 2rem;
  padding: 20px;
  border-radius: 10px;
}

button {
  background: skyblue;
  border: 1px dotted black;
  border-radius: 5px;
  padding: 5px;
}

input {
  width: 40vw;
  text-align: center;
  padding: 5px;
  border-radius: 15px;
 
}

.App {
  width: 80vw;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2rem;
}

.counts {
  display: flex;
  gap: 5vw;
}